<template>
  <div>
    <div
      class="texto"
      style="text-align: center; margin-bottom: 60px; margin-top: 15vw"
    >
      <h2>Aguarde, estamos autenticando seu acesso!</h2>
    </div>
    <div class="load" v-loading="load"></div>
  </div>
</template>

<script>
import Auth from "../../services/Auth";
import { Base64 } from "js-base64";
export default {
  name: "login",
  components: {},
  methods: {
    saveInternal(user) {
      localStorage.setItem("SELECTED_ACCOUNT_ID", JSON.stringify(user.ID));
      localStorage.setItem(
        "SELECTED_ACCOUNT_NICKNAME",
        JSON.stringify(user.NICKNAME)
      );
      localStorage.setItem("ACCOUNTS", JSON.stringify(this.dataUsers));
      if (this.UserLogin.User.NAME == null) {
        this.$notify.success({
          title: "Sucesso",
          message: "Seja Bem Vindo:  " + this.UserLogin.User.SOCIAL_REASON,
          offset: 120,
          duration: 3000,
        });
      } else {
        this.$notify.success({
          title: "Sucesso",
          message: "Seja Bem Vindo:  " + this.UserLogin.User.NAME,
          offset: 120,
          duration: 3000,
        });
      }
      this.$router.push("/dashboard");
    },
    goTO(url) {
      this.$router.push(url);
    },
    userLogin() {
      const data = {
        EMAIL: this.dados.email,
        PASSWORD: this.dados.senha,
      };
      this.load = true;
      Auth.login(data)
        .then((r) => {
          this.UserLogin = r.data;
          Auth.saveDataLocalStorage(r.data);
           localStorage.setItem("ACCOUNTS", JSON.stringify(r.data.UserAccounts[0]));
          localStorage.setItem("SELECTED_ACCOUNT_ID", this.dados.nickname_id);
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.$router.push('/dashboard');
        });
    },
  },
  data() {
    return {
      dados: null,
      dataLogin: {
        EMAIL: "",
        PASSWORD: "",
        subscribe: true,
      },
      UserLogin: null,
      load: false,
      alertSuccess: false,
      alertFail: false,
      message: "",
      validEmail: false,
      validPass: false,
      internalClientOn: false,
      internalClientSet: false,
      dataUser: null,
    };
  },
  mounted() {
    window.scrollTo(0, 0);
    if (this.$route.params.dados.length > 0) {
      this.dados = Base64.decode(this.$route.params.dados);
      this.dados = JSON.parse(this.dados);
      this.userLogin();
      console.log("ok")
    }
  },
};
</script>

<style scoped>
.erro-valid {
  color: red;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 10px;
}

.registrar {
  width: 49%;
  float: left;
  border-color: transparent;
  background: #d6d6d6;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: 0px;
}

.esqueci {
  width: 49%;
  float: right;
  border-color: transparent;
  background: #d6d6d6;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: 0px;
}

.actions {
  margin-top: 15px;
  width: 100%;
}

.login-form .form-control:focus {
  background: #d8d8d8;
  color: #272727;
  border-color: transparent;
  outline: 0;
  box-shadow: 0 0 0 0.1rem rgb(36 198 220);
}

.login {
  height: 100vh;
  background: linear-gradient(90deg, rgb(0 0 0 / 92%) 0%, rgb(0 0 0 / 71%) 100%),
    url(../../assets/images/login.jpg);
  background-size: cover;
  margin-top: -158px;
}

.login-form form {
  margin-top: 20px;
}

.login-form label {
  margin-bottom: 15px;
  font-weight: 600;
  text-transform: uppercase;
}

.login-form .form-control {
  background: #d8d8d8;
  color: #272727;
  border: transparent;
  font-weight: bold;
  height: 50px;
}

.login-form {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 500px;
  height: max-content;
  background: white;
  border-radius: 5px;
  padding: 30px;
}

.login-form .entrar {
  background: linear-gradient(
    90deg,
    rgba(36, 198, 220, 1) 0%,
    rgba(127, 33, 185, 1) 100%
  );
  background-size: 400% 400%;
  -webkit-animation: AnimationName 6s ease infinite;
  -moz-animation: AnimationName 6s ease infinite;
  -o-animation: AnimationName 6s ease infinite;
  animation: AnimationName 6s ease infinite;
  width: 100%;
  color: white;
  border: none;
  height: 50px;
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active {
  border: none;
  background: rgba(36, 198, 220, 1);
  color: #fff;
  fill: #fff;
}

@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 51%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}

@-moz-keyframes AnimationName {
  0% {
    background-position: 0% 51%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}

@-o-keyframes AnimationName {
  0% {
    background-position: 0% 51%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}

@keyframes AnimationName {
  0% {
    background-position: 0% 51%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}
</style>
<style scoped>
@media (max-width: 991px) {
  .login {
    margin-top: -120px;
  }
  .login-form {
    width: 90%;
    background: white;
    border-radius: 5px;
    padding: 15px;
  }
  .registrar {
    font-size: 12px;
  }
  .esqueci {
    font-size: 12px;
  }
  .el-notification {
    top: 0px !important;
  }
}

.el-message--error {
  top: 200px !important;
}
</style>
